'use client'

/**
 * ProductPlaceholderPage
 * ---------------------------------------------------------------------------
 * Vista provisional para productos que aún no tienen página de detalle.
 * - Extrae el `slug` desde los params para mostrarlo en el título.
 * - Ofrece un botón que redirige de nuevo a la lista de productos.
 */

import Image from 'next/image'
import { useParams, useRouter } from 'next/navigation'
import { Construction } from 'lucide-react'

export default function ProductPlaceholderPage() {
  /* --- Hooks ---------------------------------------------------------- */
  const { slug } = useParams<{ slug: string }>()      // slug dinámico de la URL
  const router = useRouter()

  /* --- Render --------------------------------------------------------- */
  return (
    <main
      className="
        min-h-screen flex flex-col items-center justify-center
        bg-gradient-to-b from-blue-200 via-blue-100 to-yellow-50
        dark:from-[#a5d7ff] dark:via-[#dff8ff] dark:to-[#fff7c0] p-6
        relative overflow-hidden
      "
    >
      {/* Cielo de fondo */}
      <div className="absolute inset-0 -z-20">
        <Image
          src="/images/sky.jpg"
          alt="springfield sky"
          fill
          className="object-cover object-top opacity-40"
        />
      </div>

      {/* Tarjeta informativa */}
      <div
        className="
          relative bg-white/90 dark:bg-[#fffbe4]/95 backdrop-blur-md
          rounded-3xl shadow-2xl p-10 w-full max-w-xl flex flex-col items-center
          gap-6 border-4 border-yellow-300
        "
      >
        {/* Icono de “en construcción” */}
        <Construction className="w-16 h-16 text-orange-600 animate-bounce" />

        {/* Título con slug legible */}
        <h1 className="font-simpson text-4xl text-blue-700 text-center drop-shadow-sm">
          {slug?.toString().replace(/-/g, ' ')}
        </h1>

        {/* Descripción */}
        <p className="text-center text-gray-700 dark:text-gray-800 text-lg max-w-md leading-snug">
          ¡Vaya, nuestra página detallada para este producto aún se está horneando
          en la cocina del Kwik-E-Mart!
        </p>

        {/* Imagen decorativa */}
        <Image
          src="/images/apu-working.png"
          alt="Apu trabajando"
          width={240}
          height={240}
          className="mx-auto select-none"
        />

        {/* Cita de Apu */}
        <p className="italic text-sm text-gray-600 dark:text-gray-700 text-center">
          “Muchas gracias, vuelva pronto” — Apu
        </p>

        {/* Vuelta a la tienda */}
        <button
          onClick={() => router.push('/productos')}
          className="
            mt-2 px-6 py-2 rounded-xl bg-green-500 hover:bg-green-600
            text-white font-semibold shadow-lg
          "
        >
          Volver a la tienda
        </button>
      </div>
    </main>
  )
}
